.video {
  background-color: $grey-dark-bg;
  padding: 122px 0 482px;
  margin: 0 0 248px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1023px) {
    padding: 52px 0 235px;
    margin: 0 0 157px;
  }

  @media (max-width: 767px) {
    padding: 85px 0 168px;
    margin: 0 0 118px;
  }
}

.video__wrapper {
  width: 100%;
  padding: 0 17%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  @media (max-width: 767px) {
    padding: 0 6%;
  }

  @media (max-width: 767px) {
    padding: 0 3%;
  }
}

.video__wrapper h2 {
  font-size: 30px;
  line-height: 30px;
  color: $white;

  @include opensans-semibold;
  margin: 0 0 20px;
  padding: 0;
  text-align: center;

  @media (max-width: 1023px) {
    font-size: 24px;
    line-height: 30px;
  }
}

.video__wrapper p {
  font-size: 16px;
  line-height: 28px;
  color: $white;
  opacity: 0.7;

  @include opensans-light;
  margin: 0;
  padding: 0 30%;
  text-align: center;

  @media (max-width: 1023px) {
    font-size: 16px;
    line-height: 28px;
    padding: 0 15%;
  }

  @media (max-width: 767px) {
    padding: 0;
  }
}

.video__container {
  text-align: center;
  position: absolute;
  top: 138%;

  @media (max-width: 767px) {
    padding: 0 6%;
    width: 100%;
  }

  @media (max-width: 767px) {
    padding: 0 4%;
  }
}

.video__container img {
  background-color: $black-input;

  @media (max-width: 1023px) {
    max-width: 503px;
  }

  @media (max-width: 767px) {
    width: 100%;
  }
}

.video__container button {
  position: absolute;
  top: 46%;
  left: 46%;
  z-index: 3;

  @media (max-width: 1023px) {
    top: 34%;
    left: 41%;
  }

  @media (max-width: 767px) {
    top: 31%;
    left: 39%;
  }
}
