.goods-types {
  background-color: $grey-bg;
  padding: 11% 0 8%;

  @media (max-width: 1023px) {
    overflow: hidden;
    padding: 9% 0;
  }

  @media (max-width: 767px) {
    padding: 38% 0 18%;
  }
}

.goods-types__wrapper {
  width: 100%;
  padding: 1% 14%;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1023px) {
    width: 100vw;
    padding: 1% 9%;
  }

  @media (max-width: 767px) {
    padding: 1% 7%;
  }
}

.goods-types__wrapper h2 {
  display: block;
  font-size: 30px;
  line-height: 30px;
  color: $black-title;

  @include opensans-semibold;
  text-align: center;
  margin: 0 0 55px;
  padding: 0;

  @media (max-width: 1023px) {
    font-size: 24px;
    line-height: 41px;
    margin: 0 0 30px;
  }
}

.goods-types__wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: space-between;

  @media (max-width: 1023px) {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 50px;
  }

  @media (max-width: 767px) {
    gap: 36px;
  }
}

.goods-types__wrapper img {
  margin: 0 0 20px;
  background-color: $grey-dark-text-modal;

  @media (max-width: 1023px) {
    width: 270px;
    height: 300px;
  }
}

.goods-types__wrapper p {
  margin: 0;
  padding: 0;
  font-size: 22px;
  line-height: 34px;
  color: $black;

  @include opensans-semibold;
  display: block;
  text-align: center;
}

.goods-types__wrapper li:first-child {
  position: relative;
  z-index: 1;
}

.goods-types__wrapper li:first-child::before {
  content: "";
  position: absolute;
  width: 180px;
  height: 180px;
  top: 160px;
  left: -65px;
  background-color: transparent;
  border: 27px solid $mint;
  border-radius: 50%;
  z-index: -1;

  @media (max-width: 1023px) {
    width: 127px;
    height: 127px;
    border: 18px solid $mint;
    top: 201px;
    left: -38px;
  }
}

.goods-types__wrapper li:first-child + li {
  padding: 35px 0 0;
  position: relative;

  @media (max-width: 1023px) {
    padding: 0;
  }
}

.goods-types__wrapper li:first-child + li::before {
  content: "";
  position: absolute;
  top: 15px;
  left: -19px;
  width: 47px;
  height: 47px;
  background-color: transparent;
  border: 8px solid $mint;
  z-index: 2;

  @media (max-width: 1023px) {
    display: none;
  }
}

.goods-types__wrapper li:first-child + li::after {
  content: "";
  position: absolute;
  bottom: 36px;
  right: -22px;
  width: 47px;
  height: 47px;
  background-color: transparent;
  border: 8px solid $mint;
  z-index: 2;

  @media (max-width: 1023px) {
    display: none;
  }
}

.goods-types__wrapper li:last-child {
  position: relative;
}

.goods-types__wrapper li:last-child::before {
  content: "";
  position: absolute;
  top: -34px;
  right: -33px;
  width: 95px;
  height: 95px;
  background-color: transparent;
  border: 17px solid $mint;
  border-radius: 50%;
  z-index: 2;

  @media (max-width: 1023px) {
    width: 68px;
    height: 68px;
    border: 13px solid $mint;
    top: -22px;
    right: -23px;
  }
}

.goods-types__wrapper li:not(:last-child) {
  margin-right: 23px;
}
